.card {
  background: var(--white);
  border-radius: 8px;
  padding: 1.5rem;
  box-shadow: var(--shadow);
}

.card-hoverable {
  cursor: pointer;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.card-hoverable:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-lg);
}